// pages/test.js
import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../../components/main-layout-en';
import Seo from "../../components/seo";

//Image
import ListBg from '../../images/bg/list.svg';
import ListBgSp from '../../images/bg/list-sp.svg';
import PdfIcon from '../../images/icons/pdf.svg';

const DocumentsPage = ({ data }) => {
  const currentDate = new Date().toISOString();
  const whitePaper = data.allMicrocmsDocuments.edges.filter(
    ({ node }) => node.public_date <= currentDate && node.category[0] === 'ホワイトペーパー'
  );
  const relatedMaterial = data.allMicrocmsDocuments.edges.filter(
    ({ node }) => node.public_date <= currentDate && node.category[0] === '関連資料'
  );
  const whitePaperVersions = whitePaper.map(({ node }) => node.whitepaper_version[0])
  const relatedMaterialTypes = relatedMaterial.map(({ node }) => node.document_type[0])
 
  data = data.allMicrocmsDocuments.edges.filter(
    ({ node }) => node.public_date <= currentDate
  );
  data.forEach(function(article){
    const date = new Date(article.node.public_date);
    const formattedDate = date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
    const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
    article.node.public_date_formatted = desiredFormat;
  });

  return <Layout>
    <section className="list white-paper">
      <picture>
          <source media="(min-width: 768px)" srcSet={ListBg} />
          <img className="list__bg" src={ListBgSp} alt="White papers・Related materials" />
      </picture>
      <div className="default-max-width">
        <h2 className="heading heading--no-line">White papers・Related materials</h2>
        <p className="list__description">Trusted Webの考え方について、ホワイトペーパーとして詳細をまとめて公開しています。<br/>日本政府は有識者と議論しながら、このホワイトペーパーを更新して、議論のたたき台にしています。</p>
        <div className="white-paper__wrapper">
          <h3 className="heading">White papers</h3>
            {
              // No version specified
              whitePaperVersions.includes(undefined)
                ? 
                <div className="white-paper__list-container">
                  <ul className="white-paper__list">
                    {data.map(edge => {
                        const article = edge.node
                        if (
                          article.category[0] === 'ホワイトペーパー' && 
                          article.whitepaper_version[0] === undefined
                        ) {
                          return (
                          <React.Fragment key={article.id}>
                            <li className="white-paper__item">
                              <div className="gold-bullet"></div>
                              <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                                {article.title_ja}
                                <img src={PdfIcon} alt="pdf"/>
                              </a>
                            </li>
                          </React.Fragment>
                          )
                        }

                        return null;
                    })}
                  </ul>
                </div>
                : null
            }
            {
              // Ver.2.0 
              whitePaperVersions.includes('Ver.2.0')
                ? 
                <div className="white-paper__list-container">
                  <h4 className="white-paper__title">Ver.2.0</h4>
                  <ul className="white-paper__list">
                    {data.map(edge => {
                        const article = edge.node
                        if (
                          article.category[0] === 'ホワイトペーパー' && 
                          article.whitepaper_version[0] === 'Ver.2.0'
                        ) {
                          return (
                          <React.Fragment key={article.id}>
                            <li className="white-paper__item">
                              <div className="gold-bullet"></div>
                              <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                                {article.title_ja}
                                <img src={PdfIcon} alt="pdf"/>
                              </a>
                            </li>
                          </React.Fragment>
                          )
                        }

                        return null;
                    })}
                  </ul>
                </div>
                : null
            }
            {
              // Ver.1.0
              whitePaperVersions.includes('Ver.1.0')
                ? 
                <div className="white-paper__list-container">
                  <h4 className="white-paper__title">Ver.1.0</h4>
                  <ul className="white-paper__list">
                    {data.map(edge => {
                        const article = edge.node
                        if (
                          article.category[0] === 'ホワイトペーパー' && 
                          article.whitepaper_version[0] === 'Ver.1.0'
                        ) {
                          return (
                          <React.Fragment key={article.id}>
                            <li className="white-paper__item">
                              <div className="gold-bullet"></div>
                              <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                                {article.title_ja}
                                <img src={PdfIcon} alt="pdf"/>
                              </a>
                            </li>
                          </React.Fragment>
                          )
                        }

                        return null;
                    })}
                  </ul>
                </div>
                : null
            }
        </div>
        <div className="white-paper__wrapper">
          <h3 className="heading">Related materials</h3>
          {
            // No document type specified
            relatedMaterialTypes.includes(undefined)
              ? 
              <div className="white-paper__list-container">
                <ul className="white-paper__list">
                  {data.map(edge => {
                      const article = edge.node
                      if (
                        article.category[0] === '関連資料' && 
                        article.document_type[0] === undefined
                      ) {
                        return (
                        <React.Fragment key={article.id}>
                          <li className="white-paper__item">
                            <div className="gold-bullet"></div>
                            <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                              {article.title_ja}
                              <img src={PdfIcon} alt="pdf"/>
                            </a>
                          </li>
                        </React.Fragment>
                        )
                      }

                      return null;
                  })}
                </ul>
              </div>
              : null
          }
          {
            // カテゴリ2
            relatedMaterialTypes.includes('カテゴリ2')
              ? 
              <div className="white-paper__list-container">
                <h4 className="white-paper__title">カテゴリ2</h4>
                <ul className="white-paper__list">
                  {data.map(edge => {
                      const article = edge.node
                      if (
                        article.category[0] === '関連資料' && 
                        article.document_type[0] === 'カテゴリ2'
                      ) {
                        return (
                        <React.Fragment key={article.id}>
                          <li className="white-paper__item">
                            <div className="gold-bullet"></div>
                            <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                              {article.title_ja}
                              <img src={PdfIcon} alt="pdf"/>
                            </a>
                          </li>
                        </React.Fragment>
                        )
                      }

                      return null;
                  })}
                </ul>
              </div>
              : null
          }
          {
            // カテゴリ1
            relatedMaterialTypes.includes('カテゴリ1')
              ? 
              <div className="white-paper__list-container">
                <h4 className="white-paper__title">カテゴリ1</h4>
                <ul className="white-paper__list">
                  {data.map(edge => {
                      const article = edge.node
                      if (
                        article.category[0] === '関連資料' && 
                        article.document_type[0] === 'カテゴリ1'
                      ) {
                        return (
                        <React.Fragment key={article.id}>
                          <li className="white-paper__item">
                            <div className="gold-bullet"></div>
                            <a className="white-paper__link" target="_blank" rel='noreferrer' href={article.url}>
                              {article.title_ja}
                              <img src={PdfIcon} alt="pdf"/>
                            </a>
                          </li>
                        </React.Fragment>
                        )
                      }

                      return null;
                  })}
                </ul>
              </div>
              : null
          }
        </div>
      </div>
    </section>
  </Layout>
}

export const query = graphql`
  {
    allMicrocmsDocuments(
      sort: { fields: [public_date, createdAt], order: DESC }
    ) {
      edges {
        node {
          id
          title_ja
          public_date
          category
          whitepaper_version
          url
          document_type
        }
      }
    }
  }
`

export default DocumentsPage

export const Head = () => (
  <Seo title="White papers・Related materials" />
)
